body {
  background-image: url(https://image.freepik.com/free-photo/black-wall-texture-background-banner-blank-dark-gradient-studio-room-chalkboard_28629-594.jpg);
}

.box {
  display: inline-block;
  border: 1px solid black;
  width: 20px;
  height: 20px;
  margin-left: -1px;
  margin-bottom: -1px;
}

.off {
  background-color: lightgray;
}

.on {
  background-color: rgb(38, 175, 91);
}

.box:hover {
  /*change color of box when mouse is hover it
    */
  background: #00ccff;
}

.center {
  margin: auto;
  display: table;
  margin-top: 10px;
}

.grid {
  width: 150px;
  line-height: 0;
  margin: auto;
  margin-top: 30px;
}

h1,
h2,
h3,
h4 {
  color: white;
  margin: auto;
  text-align: center;
  width: 100%;
  margin-top: 15px;
}

h4 {
  margin-top: 0px;
}


